	onLoad(option) {
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







		uni.getSystemInfo({
			success: (res) => {
				this.dropScreenH = this.CustomBar + 50 + uni.upx2px(88) + uni.upx2px(80)
			}
		})
	},
	onShow() {

