	computed: {

	},
	methods: {
		/* 转换px */
		px(num) {
			return uni.upx2px(num)
		},
		/* 生成随机id数 */
		getRndInteger(min, max) {
			return Math.floor(Math.random() * (max - min + 1)) + min;
		},
		/* 搜索框事件 */
		searchIcon(e) {
			let value = e.detail.value;
			uni.navigateTo({
				url: `/pages/web-view/web-view?url=https://baike.baidu.com/item/${value}`
			})
		},
		
		/* 判断数据显示个数 */
		numSwitch(val) {
			switch (true){
				case val<3:
					return 160
					break;
				case val>=4:
					return 320
					break;
				default:
					return 320
					break;
			}
		},

		/* 筛选下拉打开事件 */
		screen(e) {
			this.attrArray.forEach(i => {
				i.isActive = false;
			})
			let index = e.currentTarget.dataset.index;
			let isActive = e.currentTarget.dataset.isactive;
			this.attrData = this.attrArray[index].list;		
			this.attrIndex = index;
			this.dropScreenShow = true;
			this.selectH = this.dropScreenShow ? this.numSwitch(this.attrData.length) : 0;
			this.$set(this.attrArray[index], "isActive", !this.attrArray[index].isActive)
			// this.scrollTop = 1;
			// this.$nextTick(() => {
			// 	this.scrollTop = 0
			// });
			if (isActive) {
				this.$set(this.attrArray[index], "isActive", false)
				this.selectH = 0;
				this.dropScreenShow = false;
			}
		},
		
		/* 筛选下拉关闭事件 */
		hideDropdownList() {
			let obj = this.attrArray[this.attrIndex]
			this.$set(obj,"isActive", false)
			this.attrIndex = -1;
			this.selectH = 0;
			this.dropScreenShow = false;
		},
		/* 筛选下来子项选择事件 */
		dropdownItem(e) {
			let attrindex = this.attrIndex;
			let index = e.currentTarget.dataset.index;
			let attrName = "";
			for (let i = 0;i < this.attrData.length; i++) {
				if (i == index) {
					this.attrData[i].selected = true;
					attrName = this.attrData[i].name;
				} else {
					this.attrData[i].selected = false;
				}
			}
			let obj = this.attrArray[attrindex]
			if (obj.selectName == attrName) {
				return false;
			}
			if (attrindex == 2) {
				switch (attrName){
					case '大图模式':
						this.isBigMode = true;
						break;
					case '小图模式':
						this.isBigMode = false;
						break;
					default:
						break;
				}
			}
			this.$set(obj,"selectName", attrName)
			this.$set(obj,"isActive", false)
			this.attrIndex = -1;
			this.selectH = 0;
			this.dropScreenShow = false;
		},
		
		/* 顶部筛选下拉展开事件 */
		iconDropChange() {
			this.show = !this.show;
		},
		/* 顶部筛选下拉关闭事件 */
		handleClose() {
			this.show = false;
		},
		/* 查看产品详情 */
		checkDetail() {
			uni.navigateTo({
				url: '/pages/detail/detail?id='+this.getRndInteger(1,3)
			})
		}
	},
